.station-info {
  padding: 20px 0;
  background-color: #f2f2f2;
}
.station-info h3 {
  font-family: 'Noto Sans TC', sans-serif;
  padding: 4px 10%;
  width: 100%;
  font-weight: 100;
  letter-spacing: 1px;
}
.station-info .station-info-title-ch {
  display: inline-block;
  font-size: 36px;
  font-weight: 500;
}
.station-info .station-info-title-en {
  display: inline-block;
  font-size: 20px;
  margin-left: 10px;
}
.station-info .station-info-address {
  display: block;
  font-size: 20px;
}
.station-info iframe {
  width: 80%;
  height: 500px;
  margin: 16px auto;
  display: block;
  border: 0;
}
