.timetable-detail-container {
  margin: 0;
  padding: 40px 0 40px 80px;
}
.timetable-detail-container h2 {
  font-size: 30px;
  letter-spacing: 1px;
  border-bottom: #ff8300 solid 2px;
  width: 200px;
  margin: 0 0 30px 0;
  font-weight: 100;
  color: black;
  font-family: 'Noto Sans TC', sans-serif;
}
.timetable-detail-container h2 span {
  background-image: url('../timeTable_arrow.svg');
  background-size: 30px;
  background-repeat: no-repeat;
  display: inline-block;
  width: 30px;
  height: 30px;
  margin: 0 16px 6px 16px;
  vertical-align: middle;
}
.timetable-detail-container .train-no {
  border: none;
  color: black;
  width: 103px;
  font-size: 20px;
  margin: 0;
  font-weight: 100;
}
.timetable-detail-container div {
  width: 60px;
  height: 100px;
  display: inline-block;
  border: #ff8300 solid 2px;
  border-radius: 5px;
}
.timetable-detail-container div h3 {
  padding: 8px 0;
  font-weight: 100;
  font-family: 'Noto Sans TC', sans-serif;
  text-align: center;
}
.timetable-detail-container div .stop-name {
  font-size: 24px;
}
.timetable-detail-container div .stop-time {
  font-size: 18px;
}
.timetable-detail-container .arrow-path-sign {
  border: none;
  width: 30px;
  height: 30px;
  margin: 0 4px 16px 8px;
  background-image: url('../timeTable_arrow.svg');
  background-size: 30px;
  background-repeat: no-repeat;
}
