nav .nav-title {
  position: absolute;
  top: 0;
  left: 80px;
  width: 25%;
}
nav .nav-title h1 {
  font-family: 'Noto Sans TC', sans-serif;
  color: #555;
}
nav .nav-title .nav-ch-title {
  letter-spacing: 3px;
  font-size: 22px;
  line-height: 22px;
  padding: 6px 0;
}
nav .nav-title .nav-en-title {
  letter-spacing: 2px;
  font-size: 18px;
  line-height: 18px;
  padding: 0 0 6px 0;
}
nav .nav-item-row {
  position: absolute;
  top: 0;
  left: 32%;
  width: 60%;
  height: 60px;
}
nav .nav-item-row ul {
  width: 100%;
  height: 100%;
}
nav .nav-item-row ul li {
  list-style: none;
  width: 16%;
  height: 100%;
  text-align: center;
  color: #555;
  line-height: 60px;
  font-size: 20px;
  float: left;
}
nav .nav-item-row ul li:hover {
  cursor: pointer;
  color: #ffc17f;
}
nav .nav-item-row ul .running-state {
  pointer-events: none;
  /* color : 
                查詢狀態中：checking:#afafaf
                營運正常：stable:#1cee08
                營運異常：abnormal:#cece14
                營運停止：breakdown:#f62c2c
                */
}
nav .nav-item-row ul .running-state .running-state-image {
  float: left;
  width: 22px;
  height: 22px;
  margin-top: 19px;
  /*background-image:url('../circle.svg');*/
  background-size: cover;
  background-repeat: no-repeat;
}
nav .nav-item-row ul .running-state .running-state-describe {
  margin-left: -14px;
  /*color:#1cee08;*/
}
