.timetable-container {
  width: 866px;
  min-height: 150px;
  max-height: 500px;
  margin: 0px 0 60px 360px;
  overflow: auto;
  padding: 20px;
  border: #aaa solid 0px;
  border-radius: 5px;
}
.timetable-container h3 {
  border: #aaa solid 2px;
  display: inline-block;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 20px;
  font-weight: 100;
  color: #555;
  text-align: center;
  padding: 10px 0;
  letter-spacing: 2px;
}
.timetable-container .timetable {
  letter-spacing: 5px;
  border-top-left-radius: 4px;
  font-weight: 900;
  width: 400px;
  border-right: #aaa solid 1px;
}
.timetable-container .bound-for-south {
  font-size: 20px;
  width: 200px;
  border-left: #aaa solid 1px;
  border-right: #aaa solid 1px;
  color: #ff8300;
}
.timetable-container .bound-for-south:hover {
  cursor: pointer;
  background-color: #ffc17f;
  color: white;
}
.timetable-container .bound-for-north {
  font-size: 20px;
  border-top-right-radius: 4px;
  width: 200px;
  border-left: #aaa solid 1px;
  color: #ff8300;
}
.timetable-container .bound-for-north:hover {
  cursor: pointer;
  background-color: #ffc17f;
  color: white;
}
.timetable-container .input-container {
  width: 800px;
  border-right: #aaa solid 2px;
  border-left: #aaa solid 2px;
  border-bottom: #aaa solid 2px;
}
.timetable-container .train-no-input {
  width: 100%;
  height: 100%;
  color: #555;
  letter-spacing: 2px;
  font-size: 20px;
  padding: 10px 0 10px 10px;
  border: none;
  outline: none;
}
.timetable-container .train-no-input:focus {
  color: #ff8300;
  outline: none;
  border-color: #ff8300;
  box-shadow: 0 0 6px #ff8300;
}
.timetable-container article {
  width: 800px;
  height: 65px;
  border-left: #555 solid 1px;
  border-right: #555 solid 1px;
  border-bottom: #555 solid 1px;
}
.timetable-container article div {
  font-size: 22px;
  font-family: 'Noto Sans TC', sans-serif;
  font-weight: 100;
  text-align: center;
  display: inline-block;
  margin: 0 40px;
  color: #555;
  width: 160px;
}
.timetable-container article div span {
  color: #333;
  display: block;
  font-size: 18px;
  font-family: 'Noto Sans TC', sans-serif;
  font-weight: 100;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.timetable-container article .train-no {
  margin-left: 16px;
  width: 90px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  vertical-align: top;
}
.timetable-container article .to-sign {
  vertical-align: base;
  margin-bottom: 6px;
  width: 30px;
  height: 30px;
  background-image: url('../timeTable_arrow.svg');
  background-repeat: no-repeat;
  background-size: cover;
}
.timetable-container article:nth-child(odd) {
  background-color: #eaeaea;
}
.timetable-container article:nth-child(odd):hover {
  cursor: pointer;
  background-color: #ffc17f;
}
.timetable-container article:nth-child(even) {
  background-color: white;
}
.timetable-container article:nth-child(even):hover {
  cursor: pointer;
  background-color: #ffc17f;
}
.timetable-container .input-zero-related-result {
  padding-top: 40px;
  width: 800px;
  height: 100px;
  border: none;
}
