main#main {
  width: 100%;
  background-color: #f2f2f2;
  /*站間車票列表*/
}
main#main .station-ticket-time {
  width: 100%;
  height: 80px;
  border-bottom: #777 solid 1px;
}
main#main .station-ticket-time ul.table-head li {
  float: left;
  list-style: none;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 22px;
  color: #ccc;
  width: 14.285%;
  border-left: #777 solid 0.5px;
  border-right: #777 solid 0.5px;
  border-top: #777 solid 2px;
  border-bottom: #777 solid 2px;
  line-height: 80px;
  height: 80px;
  text-align: center;
  letter-spacing: 2px;
}
main#main .station-ticket-time ul.table-data li {
  float: left;
  list-style: none;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 20px;
  letter-spacing: 2px;
  color: white;
  width: 14.285%;
  line-height: 80px;
  height: 80px;
  text-align: center;
}
