.station-btn-group {
  padding: 20px 0 0 0;
  margin: 0 auto;
  width: 500px;
  height: 350px;
}
.station-btn-group .area-title {
  padding: 6px 0;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 22px;
  color: #ff8300;
}
.station-btn-group li {
  list-style-type: none;
  width: 100%;
  height: 30px;
  margin: 7px 20px;
}
.station-btn-group li .station-btn {
  float: left;
  margin: 0 3px;
  padding: 2px 10px;
  font-family: 'Noto Sans TC', sans-serif;
  color: #ff8300;
  background-color: white;
  border: #ff8300 solid 1px;
  border-radius: 10px;
}
.station-btn-group li .station-btn:hover {
  cursor: pointer;
  color: white;
  background-color: #ffc17f;
  border: #ffc17f solid 1px;
}
