.train-by-date-timetable-container {
  position: absolute;
  top: 60px;
  left: 580px;
  width: 720px;
  max-height: 430px;
  margin: 0;
  overflow: auto;
  padding: 20px;
  border: none;
  /*
    border:#aaa solid 2px;
    border-radius:5px;
    */
}
.train-by-date-timetable-container h3 {
  border: #aaa solid 2px;
  display: inline-block;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 20px;
  font-weight: 100;
  color: #555;
  text-align: center;
  padding: 10px 0;
  letter-spacing: 2px;
}
.train-by-date-timetable-container .train-by-date-timetable {
  letter-spacing: 5px;
  border-top-left-radius: 4px;
  font-weight: 900;
  width: 350px;
  border-right: #aaa solid 1px;
}
.train-by-date-timetable-container .train-by-date-bound-for-south {
  font-size: 20px;
  width: 150px;
  border-left: #aaa solid 1px;
  border-right: #aaa solid 1px;
  color: #ff8300;
}
.train-by-date-timetable-container .train-by-date-bound-for-south:hover {
  cursor: pointer;
  background-color: #ffc17f;
  color: white;
}
.train-by-date-timetable-container .train-by-date-bound-for-north {
  font-size: 20px;
  border-top-right-radius: 4px;
  width: 150px;
  border-left: #aaa solid 1px;
  color: #ff8300;
}
.train-by-date-timetable-container .train-by-date-bound-for-north:hover {
  cursor: pointer;
  background-color: #ffc17f;
  color: white;
}
.train-by-date-timetable-container .train-by-date-input-container {
  width: 650px;
  border-right: #aaa solid 2px;
  border-left: #aaa solid 2px;
  border-bottom: #aaa solid 2px;
}
.train-by-date-timetable-container .train-by-date-number-input {
  width: 100%;
  height: 100%;
  color: #555;
  letter-spacing: 2px;
  font-size: 20px;
  padding: 10px 0 10px 10px;
  border: none;
  outline: none;
}
.train-by-date-timetable-container .train-by-date-number-input:focus {
  color: #ff8300;
  outline: none;
  border-color: #ff8300;
  box-shadow: 0 0 6px #ff8300;
}
.train-by-date-timetable-container .zero-related-result {
  padding-top: 30px;
  width: 650px;
  height: 100px;
}
.train-by-date-timetable-container article {
  width: 650px;
  height: 65px;
  border-left: #555 solid 1px;
  border-right: #555 solid 1px;
  border-bottom: #555 solid 1px;
}
.train-by-date-timetable-container article div {
  font-size: 20px;
  font-family: 'Noto Sans TC', sans-serif;
  font-weight: 100;
  text-align: center;
  display: inline-block;
  margin: 0 20px;
  color: #555;
  width: 160px;
}
.train-by-date-timetable-container article div span {
  color: #333;
  display: block;
  font-size: 18px;
  font-family: 'Noto Sans TC', sans-serif;
  font-weight: 100;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.train-by-date-timetable-container article .train-number {
  margin-left: 16px;
  width: 90px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  vertical-align: top;
}
.train-by-date-timetable-container article .to-sign {
  vertical-align: base;
  margin-bottom: 6px;
  width: 30px;
  height: 30px;
  background-image: url('../timeTable_arrow.svg');
  background-repeat: no-repeat;
  background-size: cover;
}
.train-by-date-timetable-container article:nth-child(odd) {
  background-color: #eaeaea;
}
.train-by-date-timetable-container article:nth-child(odd):hover {
  cursor: pointer;
  background-color: #ffc17f;
}
.train-by-date-timetable-container article:nth-child(even) {
  background-color: white;
}
.train-by-date-timetable-container article:nth-child(even):hover {
  cursor: pointer;
  background-color: #ffc17f;
}
