.news-container {
  margin: 30px auto;
  width: 70%;
  min-height: 350px;
  overflow: auto;
}
.news-container article {
  width: 100%;
  height: 40px;
}
.news-container article span {
  width: 10%;
  height: 100%;
  font-size: 16px;
  font-family: 'Noto Sans TC', sans-serif;
  color: #555;
  line-height: 40px;
  margin: 0 32px 0 16px;
  vertical-align: top;
}
.news-container article p {
  width: 80%;
  font-size: 16px;
  font-family: 'Noto Sans TC', sans-serif;
  line-height: 40px;
  color: #555;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.news-container article p a {
  color: #555;
  width: 100%;
  text-decoration: none;
}
.news-container article p a:hover {
  text-decoration: underline;
  color: #ff8300;
}
.news-container article:nth-child(odd) {
  background-color: #eaeaea;
}
.news-container article:nth-child(even) {
  background-color: white;
}
