.daypicker-container .daypicker-plugin {
  margin-left: 50px;
  min-height: 460px;
  outline: none;
}
.daypicker-container .submit-btn {
  position: absolute;
  top: 320px;
  left: 390px;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 20px;
  text-align: center;
  padding: 10px 0;
  width: 120px;
  color: #ff8300;
  background-color: white;
  border: #ff8300 solid 1px;
  border-radius: 10px;
}
.daypicker-container .submit-btn:hover {
  cursor: pointer;
  color: white;
  background-color: #ffc17f;
  border: #ffc17f solid 1px;
}
.daypicker-container .timetable-container {
  width: 716px;
  min-height: 300px;
  max-height: 540px;
  position: absolute;
  top: 80px;
  left: 200px;
  overflow: auto;
  padding: 20px;
  border: #aaa solid 2px;
  border-radius: 5px;
}
.daypicker-container .timetable-container h3 {
  border: #aaa solid 2px;
  display: inline-block;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 20px;
  font-weight: 100;
  color: #555;
  text-align: center;
  padding: 10px 0;
  letter-spacing: 2px;
}
.daypicker-container .timetable-container .timetable {
  letter-spacing: 5px;
  border-top-left-radius: 4px;
  font-weight: 900;
  width: 350px;
  border-right: #aaa solid 1px;
}
.daypicker-container .timetable-container .bound-for-south {
  font-size: 20px;
  width: 150px;
  border-left: #aaa solid 1px;
  border-right: #aaa solid 1px;
  color: #ff8300;
}
.daypicker-container .timetable-container .bound-for-south:hover {
  cursor: pointer;
  background-color: #ffc17f;
  color: white;
}
.daypicker-container .timetable-container .bound-for-north {
  font-size: 20px;
  border-top-right-radius: 4px;
  width: 150px;
  border-left: #aaa solid 1px;
  color: #ff8300;
}
.daypicker-container .timetable-container .bound-for-north:hover {
  cursor: pointer;
  background-color: #ffc17f;
  color: white;
}
.daypicker-container .timetable-container .input-container {
  width: 650px;
  border-right: #aaa solid 2px;
  border-left: #aaa solid 2px;
  border-bottom: #aaa solid 2px;
}
.daypicker-container .timetable-container .train-no-input {
  width: 100%;
  height: 100%;
  color: #555;
  letter-spacing: 2px;
  font-size: 20px;
  padding: 10px 0 10px 10px;
  border: none;
  outline: none;
}
.daypicker-container .timetable-container .train-no-input:focus {
  color: #ff8300;
  outline: none;
  border-color: #ff8300;
  box-shadow: 0 0 6px #ff8300;
}
.daypicker-container .timetable-container article {
  width: 650px;
  height: 65px;
  border-left: #555 solid 1px;
  border-right: #555 solid 1px;
  border-bottom: #555 solid 1px;
}
.daypicker-container .timetable-container article div {
  font-size: 18px;
  font-family: 'Noto Sans TC', sans-serif;
  font-weight: 100;
  text-align: center;
  display: inline-block;
  margin: 0 20px;
  color: #555;
  width: 140px;
}
.daypicker-container .timetable-container article div span {
  color: #333;
  display: block;
  font-size: 16px;
  font-family: 'Noto Sans TC', sans-serif;
  font-weight: 100;
  text-align: center;
  height: 30px;
  line-height: 30px;
}
.daypicker-container .timetable-container article .train-no {
  margin-left: 16px;
  width: 90px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  vertical-align: top;
}
.daypicker-container .timetable-container article .to-sign {
  vertical-align: base;
  margin-bottom: 6px;
  width: 30px;
  height: 30px;
  background-image: url('../timeTable_arrow.svg');
  background-repeat: no-repeat;
  background-size: cover;
}
.daypicker-container .timetable-container article:nth-child(odd) {
  background-color: #eaeaea;
}
.daypicker-container .timetable-container article:nth-child(odd):hover {
  cursor: pointer;
  background-color: #ffc17f;
}
.daypicker-container .timetable-container article:nth-child(even) {
  background-color: white;
}
.daypicker-container .timetable-container article:nth-child(even):hover {
  cursor: pointer;
  background-color: #ffc17f;
}
.daypicker-container .timetable-container .input-zero-related-result {
  padding-top: 40px;
  width: 800px;
  height: 100px;
  border: none;
}
