.ticket-class {
  background-color: #f2f2f2;
  width: 100%;
  height: 220px;
  margin: 0 auto;
  position: relative;
}
.ticket-class article {
  position: absolute;
  z-index: 100;
  width: 50%;
  height: 100%;
  padding: 30px 0 ;
}
.ticket-class article ul {
  width: 350px;
  height: 40px;
  line-height: 40px;
  margin: 0 auto;
  position: relative;
  /*票價車站*/
  /*票價列表*/
}
.ticket-class article ul li {
  text-align: center;
  font-family: 'Noto Sans TC', sans-serif;
  list-style: none;
}
.ticket-class article ul .fare-origin-ch {
  position: absolute;
  font-size: 30px;
  left: 30px;
}
.ticket-class article ul .path-sign {
  position: absolute;
  width: 50px;
  height: 100%;
  background-image: url("../arrow.svg");
  background-repeat: no-repeat;
  background-size: 50px 100%;
  margin-top: 3px;
  left: 115px;
}
.ticket-class article ul .fare-dest-ch {
  position: absolute;
  font-size: 30px;
  left: 190px;
}
.ticket-class article ul .fare-origin-en {
  margin-top: -5px;
  font-size: 18px;
  position: absolute;
  left: 35px;
}
.ticket-class article ul .fare-dest-en {
  margin-top: -5px;
  font-size: 18px;
  position: absolute;
  left: 193px;
}
.ticket-class article ul .fare-title {
  position: absolute;
  left: 0;
  font-size: 24px;
  font-weight: 100;
  border-bottom: #ff8300 solid 2px;
}
.ticket-class article ul .fare-price {
  position: absolute;
  left: 200px;
  font-size: 24px;
  font-weight: 100;
  border-bottom: #ff8300 solid 2px;
}
.ticket-class article .fare-station-ch {
  width: 300px;
  border-bottom: #ff8300 solid 2px;
}
.ticket-class article .fare-station-en {
  width: 300px;
}
.ticket-class .fare-station-path {
  left: 0;
  top: 0;
}
.ticket-class .fare-table {
  right: 0;
  top: 0;
}
