.origin-to-dest-station-panel {
  width: 900px;
  height: 450px;
  margin: 0 auto;
  position: relative;
}
.origin-to-dest-station-panel .submit-btn {
  position: absolute;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 20px;
  bottom: 30px;
  left: 770px;
  text-align: center;
  padding: 10px 0;
  width: 120px;
  color: #ff8300;
  background-color: white;
  border: #ff8300 solid 1px;
  border-radius: 10px;
}
.origin-to-dest-station-panel .submit-btn:hover {
  cursor: pointer;
  color: white;
  background-color: #ffc17f;
  border: #ffc17f solid 1px;
}
.origin-to-dest-station-panel article {
  position: absolute;
  width: 400px;
  height: 100%;
}
.origin-to-dest-station-panel article .info-title {
  position: absolute;
  left: 50px;
  font-family: 'Noto Sans TC', sans-serif;
  width: 300px;
  height: 40px;
  color: #555;
  font-size: 26px;
  background-color: white;
  border-bottom: #555 solid 1px;
  text-align: center;
  line-height: 40px;
}
.origin-to-dest-station-panel .origin {
  top: 0;
  left: 150px;
}
.origin-to-dest-station-panel .dest {
  top: 0;
  left: 520px;
}
.origin-to-dest-station-panel .station-btn-group {
  position: absolute;
  top: 30px;
  left: 50px;
  width: 100%;
}
.origin-to-dest-station-panel .station-btn-group .area-title {
  padding: 6px 0;
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 22px;
  color: #ff8300;
}
.origin-to-dest-station-panel .station-btn-group li {
  list-style-type: none;
  width: 100%;
  height: 30px;
  margin: 7px 20px;
}
.origin-to-dest-station-panel .station-btn-group li .station-btn {
  float: left;
  margin: 0 3px;
  padding: 2px 10px;
  font-family: 'Noto Sans TC', sans-serif;
  color: #ff8300;
  background-color: white;
  border: #ff8300 solid 1px;
  border-radius: 10px;
}
.origin-to-dest-station-panel .station-btn-group li .station-btn:hover {
  cursor: pointer;
  color: white;
  background-color: #ffc17f;
  border: #ffc17f solid 1px;
}
