footer {
  width: 100%;
  height: 220px;
  background-color: #ff8300;
  position: relative;
}
footer .footer-logo {
  border: #555 solid 3px;
  border-radius: 10px;
  padding: 10px 20px;
  position: absolute;
  top: 10px;
  left: 80px;
}
footer .footer-logo h1 {
  font-family: 'Noto Sans TC', sans-serif;
  font-weight: 100;
}
footer .footer-logo .footer-ch-logo {
  font-size: 20px;
}
footer .footer-logo .footer-en-logo {
  font-size: 16px;
}
footer span {
  font-family: 'Noto Sans TC', sans-serif;
  display: block;
  width: 320px;
}
footer .footer-author-github {
  position: absolute;
  bottom: 40px;
  right: 80px;
  text-align: right;
  font-size: 14px;
}
footer .footer-author-github a {
  text-decoration: none;
  color: black;
}
footer .footer-author-github a:hover {
  text-decoration: underline;
}
footer .footer-author {
  position: absolute;
  bottom: 64px;
  right: 80px;
  text-align: right;
  font-size: 14px;
}
footer .footer-author-email {
  position: absolute;
  bottom: 16px;
  right: 80px;
  text-align: right;
  font-size: 14px;
}
footer .footer-claim-ch {
  position: absolute;
  bottom: 70px;
  left: 80px;
  font-size: 10px;
  line-height: 10px;
}
footer .footer-claim-en {
  position: absolute;
  bottom: 16px;
  left: 80px;
  font-size: 10px;
  line-height: 10px;
}
