.panel-class {
  float: left;
  margin: 20px 0 0 80px;
  width: 120px;
  height: 200px;
}
.panel-class ul li {
  list-style: none;
  text-align: left;
  height: 40px;
  width: 120px;
  line-height: 40px;
  border-bottom: #555 solid 1px;
  font-size: 22px;
  font-family: 'Noto Sans TC', sans-serif;
  color: #555;
}
.panel-class ul li:hover {
  cursor: pointer;
  color: #ffc17f;
  border-bottom: #ffc17f solid 1px;
}
